@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Raleway', sans-serif;
  overflow-x:hidden;
  background-color: rgb(26, 26, 26);
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

.App-link {
  color: #09d3ac;
}


.home-class{
    width:100%;
    height: 100vh;
    
    background:url(/images/first.jpg);
    background-size: cover; /* Resize the background image to cover the entire container */
    background-position: center; /* Center the image */
    background-repeat: no-repeat;
    
}
.home-container-class{
    display:flex;
    justify-content:space-around;
}
.home-container-class div{
    display:block;
    margin-top:25vh;
}
.home-container-class div button{
    border: 2px solid rgb(255, 255, 255);  
    background-color: Transparent;
    color: white;
    text-decoration: none;
    display: inline-block;
    height: 3rem;
    width: 3rem;
    font-size: x-large;
    border-radius: 50%;
    text-align: top;

}
.home-container-class div button:hover{

    -webkit-transition-duration: 0.5s;

            transition-duration: 0.5s;
    background-color: rgb(249, 87, 255);
    color:white;
}
.header-class img{
  position: fixed;
  width: auto;
  height: auto;
  z-index: 100;
  top: 2%;
  left: 2%;
}
.home-ad-class{
    width:100vw;
    max-width: 100%;
    height: 100vh;

    background-image:
    -webkit-gradient(linear, left top, left bottom, from(rgb(32, 32, 32)),color-stop(30%, rgb(32, 32, 32)),color-stop(60%, rgba(32, 32, 32, 0.774)),color-stop(80%, rgba(32, 32, 32, 0.637)), to(rgba(39, 39, 39, 0.151))),
    url(/artlist-test/static/media/drums.e38a00dd.jpg);

    background-image:
    linear-gradient(to bottom, rgb(32, 32, 32),rgb(32, 32, 32)30%,rgba(32, 32, 32, 0.774)60%,rgba(32, 32, 32, 0.637)80%, rgba(39, 39, 39, 0.151) 100%),
    url(/artlist-test/static/media/drums.e38a00dd.jpg);
 

    background-size: cover; /* Resize the background image to cover the entire container */
    background-position: center; /* Center the image */
    overflow: auto;
}

.home-ad-cards-class{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
}

.person-card-class{
    height: 250px;
    width:200px;

  
}
.person-card-class div{
    height:100%;
    width:100%;
    background-size: cover; /* Resize the background image to cover the entire container */
    background-position: center; /* Center the image */
    
}

.person-card-bootstrap-class{
    margin-right: 1rem;
    max-width: 270px;
    max-height: 350px;
    margin-bottom: 1rem;
}
.person-card-information-bootstrap-class>p{
    font-size: 24px;
    font-weight: 900;
}
.person-card-information-bootstrap-class>div>p{
    font-size: 14px;
}
@media screen and (max-width: 900px) {
    .person-card-bootstrap-class{
        margin-right: 1rem;
        margin-left: 1rem;
        max-width: 150px;
        max-height: 250px;
    }
    .person-card-information-bootstrap-class p{
        font-size: 12px;
    }
    
    .person-card-information-bootstrap-class>div>p{
        font-size: 12px;
    }
  }
.person-card-information-bootstrap-class{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    justify-items: flex-end;
    text-align: left;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
}
.person-card-information-bootstrap-class div{
    display: flex;
    flex-direction: row;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
    
}


.home3-class{
    width:100vw ;
    
    background:url(/images/page3.png);
    background-size: cover; /* Resize the background image to cover the entire container */
    background-position: center; /* Center the image */
    background-repeat: no-repeat;
    
}
.home3-container-class{
    display:flex;
    flex-wrap: wrap;
    justify-content:space-around;
    margin-left: 1rem;
    
}
.home3-container-class div{
    display:block;
    margin-top:25vh;
    margin-bottom: 25vh;
}
.home3-container-class>div button{
    border: 2px solid rgb(255, 255, 255);  
    background-color: Transparent;
    color: white;
    text-decoration: none;
    display: inline-block;
    height: 3rem;
    width: 3rem;
    font-size: x-large;
    border-radius: 50%;
    text-align: top;

}
.home3-container-class div button:hover{

    -webkit-transition-duration: 0.5s;

            transition-duration: 0.5s;
    background-color: rgb(249, 87, 255);
    color:white;
}
@media screen and (max-width: 900px) {

    .home3-container-class{
        text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
    }
    .filler-class{
        display: none;
    }
}
.footer-class{
    display:flex;
    width: 100%;
    height: 5rem;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

}
.footer-class>div p{
    color:white;
    font-size: 20px;
}
